"use client";

import { useEffect, useState } from "react";

export default function SalesPage() {
  const [sales, setSales] = useState<any[]>([]);
  const [totals, setTotals] = useState<any[]>([]);

  useEffect(() => {
    const fetchSales = async () => {
      const resSales = await fetch("http://localhost:5000/api/sales/all", {
        credentials: "include",
      });
      const salesData = await resSales.json();
      setSales(salesData);

      const resTotals = await fetch("http://localhost:5000/api/sales/totals", {
        credentials: "include",
      });
      const totalsData = await resTotals.json();
      setTotals(totalsData);
    };
    fetchSales();
  }, []);

  return (
    <div>
      <h1>All Sales</h1>
      <table border={1}>
        <thead>
          <tr>
            <th>Attendant</th>
            <th>Fuel</th>
            <th>Quantity</th>
            <th>Amount</th>
            <th>Pump</th>
            <th>Date</th>
          </tr>
        </thead>
        <tbody>
            {sales.map((s: {
            id: number;
            attendant?: { name: string };
            fuelType?: { name: string };
            quantitySold: number;
            totalAmount: number;
            pumpNumber: number;
            createdAt: string;
            }) => (
            <tr key={s.id}>
              <td>{s.attendant?.name}</td>
              <td>{s.fuelType?.name}</td>
              <td>{s.quantitySold}</td>
              <td>{s.totalAmount}</td>
              <td>{s.pumpNumber}</td>
              <td>{new Date(s.createdAt).toLocaleString()}</td>
            </tr>
            ))}
        </tbody>
      </table>

      <h2>Totals per Attendant</h2>
      <table border={1}>
        <thead>
          <tr>
            <th>Attendant</th>
            <th>Total Quantity</th>
            <th>Total Amount</th>
            <th>Total Sales</th>
          </tr>
        </thead>
        <tbody>
          {totals.map(t => (
            <tr key={t.attendantId}>
              <td>{t.attendant?.name}</td>
              <td>{t.totalQuantity}</td>
              <td>{t.totalAmount}</td>
              <td>{t.totalSales}</td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}
